import MenuIcon from '@mui/icons-material/Menu'
import {
  AppBar,
  Box,
  Button,
  IconButton,
  Menu,
  MenuItem,
  Toolbar,
  Typography,
  Stack
} from '@mui/material'
import * as React from 'react'
import { scroller } from 'react-scroll'
import InstagramIcon from '@mui/icons-material/Instagram'

function NavBar() {

  return (
    <AppBar color="transparent" position="static" elevation={0} >
      <Toolbar>
            <Box sx={{flexGrow: {md: 0  , xs: 1}}}></Box>
          <Typography
            align="center"
            color="black.main"
            sx={{
              fontSize: 'large',
              paddingLeft: { md: '55px' },
              fontWeight: 'bold',
              paddingBottom: '5px',
              textAlign: 'center',
              lineHeight: '1',
              letterSpacing: '4px'
            }}
          >
            Birgitta Alneroth
          </Typography>
          <Box sx={{flexGrow: {md: 0, xs: 1}}}></Box>

      </Toolbar>
    </AppBar>
  )
}

export default NavBar
