import React, { useEffect } from 'react'
import { Box, Grid, Card, CardContent, Typography, Image } from '@mui/material'
import Aos from 'aos'
import 'aos/dist/aos.css'

function Gallery() {
  useEffect(() => {
    Aos.init({ duration: 1200 })
  }, [])


  var images = [];
  for (var i=0; i < 16; i++) {
    images.push(process.env.PUBLIC_URL + "/" + i + ".jpg");
  }
  return (
    <Box sx={{
        display: "flex", 
        flexWrap: "wrap", 
        justifyContent: "space-around",
        alignItems: "center", 
        alignContent: "center",
        rowGap: "30px"}}>
        {images.map(function(object, i){
            return <Box
            sx={{
              height: {md:"30vw", xs:"100vw"},
              width: {md:"22vw", xs: "80vw"},
              backgroundRepeat: "no-repeat",
              backgroundImage: `
              url(${object})`,
              backgroundSize: "cover",
              backgroundPosition: "center",
            }}
          ></Box>;
})}

    </Box>
  )
}

export default Gallery
